<script>
import { mapState, mapActions } from "vuex";
import { propertyService } from "@/services";
var $ = require('jquery')

import { Handler } from "vue-handler-panels";

import MainPanel from "./Main";

import PropertyListing from "./Listing";

export default {
  data() {
    return {
      id: this.$route.query.id,
      property: null
    };
  },
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    })
  },
  created() {
    // this.getByID(this.id);
  },
  mounted() {
    this.handler = this.$refs.handler;

    this.handler.registerPanel("main", MainPanel);
    this.handler.registerPanel("listing", PropertyListing);

    this.handler.openNextPanel("listing", $vm => {
      // $vm.setData({
      //   value: "ALPHA"
      // });
    });

    this.handler.$on("hook:updated", x => {
      this.autoScroll()
    });
  },
  methods: {
    setAuthApi(id) {
      window.location.href = "/?auth_application_id=" + id;
    },
    ...mapActions("properties", {
      //
    }),
    onClick(property) {
      this.$router.push("/property/edit?id=" + property.id);
    },
    getByID(id) {
      propertyService.getById(id).then(({ data }) => {
        this.property = data.data;
      }).catch(x=> {
        // console.log(x)
      });
    },

    autoScroll() {
      this.$el.scrollLeft = 10000;
    }
  },
  components: {
    Handler
  }
};
</script>

<template>
  <div class="bg-panels">
    <Handler ref="handler"/>
  </div>
</template>

<style lang="scss">
div.bg-panels {
  background: #e6e8ec;
  width: 100%;
  display: flex;
  scroll-behavior: smooth;
  overflow-x: scroll;
}

.content-wrapper {
  padding: 15px 30px;
}

.panel {
  max-width: 1000px;
  h1.title {
    margin-left: 0px;
    margin-top: 0px;
    color: #4e5052;
    font-size: 19px;
    font-weight: 400;
    font-family: roboto;
  }
}

#page-home {
  ul.properties {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #e4e5e6;

    & > li {
      padding: 10px;

      border-bottom: 1px solid #e4e5e6;
      background: #fafafb;
      padding: 10px;
      display: flex;

      & > .img {
        height: 50px;
        width: 50px;
        background: #fff;
        border: 1px solid #eee;

        display: flex;
        justify-content: center;
        align-items: center;

        .fas,
        .fab {
          color: #e8eaec;
          font-size: 2em;
        }
      }
      & > .content {
        margin-left: 10px;
        .title {
          font-weight: 500;
          font-family: roboto;
          font-size: 15px;
        }

        button {
          margin-top: 5px;
          width: 100px;
        }
      }

      &.focus {
        background: #fff;
        border-left: 3px solid #5891d4;
        button {
          display: none;
        }
      }
    }
  }
}
</style>



