<script>
import { Panel } from "vue-handler-panels";
import { Methods } from "vue-handler-panels";
import FilterHandler from "@/lib/filter-handler/FilterHandler";
import { mapState, mapActions } from "vuex";
import { propertyService } from "@/services";

import MainPanel from "./Main";

export default {
  data() {
    return {
      data: {},
      componentKey: "",
      search: "",
      filtered: [],
      selectedPropertyID: "",
      panel: {
        title: "Listing",
        width: "500"
      },
      rootData: {},
      loading: true,
      properties: {}
    };
  },
  computed: {
    ...mapState({
      auth: state => state.auth,
      application: state => state.auth.application
    })
  },
  created() {
    //
    // SETTING FILTER HANDLER
    this.$filter = new FilterHandler({
      propsData: {
        parent: this,
        data: {
          234: {
            label: "teste"
          }
        }
      }
    });

    this.$filter.$on("filter", data => {
      console.log("EXIBIR", data);
      this.filtered = data;
    });

    // BEFORE READY REGISTERING OPTIONS PANEPS
    this.$on("before-ready", controller => {
      controller.registerPanel("editor", MainPanel);
    });

    this.sync();
  },
  methods: {
    ...Methods,

    setData(data) {
      //
      this.data = data;

      for (var key in this.data) {
        if (this.data[key] instanceof Object) {
          this.$set(this.data[key], "export", undefined);
        } else {
          delete this.data[key];
        }
      }

      this.$filter.data = this.data;
    },
    getData() {
      return this.data;
    },
    select(id) {
      if (this.selectedPropertyID == id) {
        this.controller.next($vm => {
          this.selectedPropertyID = "";
          $vm.close();
        });
        return;
      }
      this.selectedPropertyID = id;

      this.openEditor();
    },
    openEditor() {
      this.controller.openNext("editor", $vm => {
        $vm.id = this.selectedPropertyID;
        $vm.$on("hook:beforeDestroy", x => {
          this.$nextTick(x => {
            if (this.selectedPropertyID) {
              this.selectedPropertyID = "";
            }
          });
        });
      });
    },
    getSelectedItem() {
      return this.data[this.selectedPropertyID]
        ? this.data[this.selectedPropertyID]
        : null;
    },
    addNew() {
      var title = prompt("Ttulo do imvel");

      if (!title) {
        return;
      }

      var data = {
        attributes: {
          title: {
            data: {
              value: title
            }
          }
        }
      };

      propertyService.insert(data).then(data => {
        alert("Imvel adicionaro com sucesso!");
        this.sync();
      });
    },
    removeProperty(key) {
      if (confirm("Deletar imvel '" + key + "'?")) {
        // this.controller.next((next, $vm) => {
        //   if ($vm.data.key == key) {
        //     next.close(true);
        //   }
        // });
        propertyService
          ._delete({
            id: key
          })
          .then(x => {});
      }
    },
    sync() {
      this.loading = true;
      // this.controller.next($vm => {
      //   this.selectedPropertyID = "";
      //   $vm.close();
      // });
      propertyService.getAll().then(({ data }) => {
        this.loading = false;
        this.properties = data.data;
      });
    }
  },
  components: {
    Panel
  },
  watch: {
    search(search) {
      this.$filter.query = search;
    },
    properties(data) {
      if (data) {
        var items = {};
        for (var i in data) {
          var row = data[i];
          items[row.id] = row;
        }
        this.$filter.data = items;
        this.$filter.index();
        this.$filter.filter();
      }
    }
  }
};
</script>
<template>
  <Panel title="Meus imveis" icon="fas fa-home" :settings="panel">
    <div class="panel-search">
      <input type="text" class="form-control search" placeholder="Filtrar" v-model="search">
      <button @click="sync" class="btn btn-sm btn-default ml-3" :disabled="loading">
        <i class="fas fa-sync"></i>
      </button>
      <button @click="addNew" class="btn btn-sm btn-default ml-3">
        <span>Novo</span>&nbsp;
        <i class="fas fa-plus"></i>
      </button>
    </div>
    <div class="loading" v-if="loading"> Carregando imoveis...</div>
    <div class="panel-listing" v-if="!loading">
      <ul class="listing" v-if="filtered">
        <li
          v-for="(row, index) in filtered"
          :key="index"
          :class="{selected: selectedPropertyID==row.key}"
        >
          <ul class="optionsbar">
            <li @click="removeProperty(row.key)">
              <i class="fas fa-times"></i>
            </li>
          </ul>
          <div class="content" @click="select(row.key)">
            <div class="title">{{row.data.title ? row.data.title : ' Sem ttulo'}}</div>
            <div class="info">{{row.key}}</div>
          </div>
        </li>
      </ul>
    </div>
  </Panel>
</template>

<style lang="scss" scoped>
.loading {
  font-weight: 500;
  padding: 15px;
  font-family: roboto;
  color: #525354;
  display: flex;
}
.panel-listing {
  display: flex;

  ul.listing {
    flex-shrink: 1;
    overflow-y: auto;
    flex-grow: 1;
    border-top: 1px solid #e2e2e2;

    & > li {
      position: relative;
      border-bottom: 1px solid #eee;
      cursor: pointer;
      user-select: none;

      &.export {
        background: #f5f6f7 !important;
        button {
          display: none;
        }
      }
      & > .content {
        color: #444;
        font-family: roboto;
        // background: red;
        padding: 5px 10px;

        .title {
          font-weight: bold;
        }
      }

      & > .optionsbar {
        z-index: 1;
        position: absolute;
        right: 0;
        background: #eaebed;
        display: none;
        border-bottom: 1px solid #ddd;
        border-left: 1px solid #ddd;

        li {
          width: 20px;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #789;
        }
      }

      &:hover {
        .optionsbar {
          display: flex;
        }
      }

      &.selected {
        border-left: 4px solid #5891d4;
      }
      &.selected.export {
        // background: #ecedee !important;
      }
    }
  }
}
</style>


